"use strict";
const _ = require('lodash');
const chalk = require('chalk');
const TaskConfig_1 = require('./TaskConfig');
const utils_1 = require('./utils');
/**
 * convert setup task result to run sequence src.
 *
 * @export
 * @param {Gulp} gulp
 * @param {ITask[]} tasks
 * @param {ITaskContext} ctx
 * @returns {Src[]}
 */
function toSequence(gulp, tasks, ctx) {
    let seq = [];
    let len = tasks.length;
    if (len < 1) {
        return seq;
    }
    tasks = utils_1.sortOrder(tasks, t => t.getInfo().order);
    let hasWatchtasks = [];
    _.each(tasks, t => {
        let info = t.getInfo();
        if (info.oper & ctx.oper) {
            let tname = t.setup(ctx, gulp);
            if (tname) {
                if ((info.oper & TaskConfig_1.Operation.watch)) {
                    hasWatchtasks.push(tname);
                }
                registerTasks(ctx, tname);
                seq.push(tname);
            }
        }
    });
    let watchname = taskSequenceWatch(gulp, seq, ctx, it => {
        if (!it) {
            return false;
        }
        if (hasWatchtasks.length > 0) {
            return hasWatchtasks.indexOf(it) < 0;
        }
        return true;
    });
    if (watchname) {
        registerTasks(ctx, watchname);
        seq.push(watchname);
    }
    return seq;
}
exports.toSequence = toSequence;
function registerTasks(ctx, tasks) {
    ctx.globals.tasks = ctx.globals.tasks || {};
    if (_.isArray(tasks)) {
        _.each(tasks, t => registerGlobals(ctx, t));
    }
    else {
        registerGlobals(ctx, tasks);
    }
}
function registerGlobals(ctx, task) {
    if (ctx.globals.tasks[task]) {
        console.error(chalk.red('has same task:'), chalk.cyan(task));
        process.exit(0);
    }
    else {
        ctx.globals.tasks[task] = task;
    }
}
/**
 * generate watch task for sequence
 *
 * @export
 * @param {Gulp} gulp
 * @param {Src[]} tasks
 * @param {ITaskContext} ctx
 * @param {(str: string) => boolean} [express]
 * @returns
 */
function taskSequenceWatch(gulp, tasks, ctx, express) {
    // create watch task.
    if ((ctx.oper & TaskConfig_1.Operation.watch) && ctx.option.watch) {
        let wats = [];
        let name = '';
        if (_.isBoolean(ctx.option.watch)) {
            let toWatchSeq = filterTaskSequence(tasks, express);
            if (toWatchSeq.length > 1) {
                let first = _.first(toWatchSeq);
                first = _.isArray(first) ? _.first(first) : first;
                let last = _.last(toWatchSeq);
                last = _.isArray(last) ? _.last(last) : last;
                name = `${first}-${last}`;
                let taskname = name + '-seq';
                gulp.task(taskname, () => {
                    return runSequence(gulp, toWatchSeq);
                });
                wats.push(taskname);
            }
            else if (toWatchSeq.length === 1) {
                let first = _.first(toWatchSeq);
                if (_.isArray(first)) {
                    let fs = _.first(first);
                    let ls = _.last(first);
                    name = `${fs}-${ls}`;
                    wats = first;
                }
                else if (first) {
                    name = first;
                    wats.push(first);
                }
            }
        }
        else {
            wats = ctx.option.watch;
        }
        if (wats.length > 0) {
            name = name ? name + '-owatch' : 'owatch';
            gulp.task(name, () => {
                let src = ctx.getSrc();
                console.log('watch, src:', chalk.cyan.call(chalk, src));
                gulp.watch(src, wats);
            });
            return name;
        }
    }
    return '';
}
exports.taskSequenceWatch = taskSequenceWatch;
/**
 * add task to task sequence.
 *
 * @export
 * @param {Src[]} taskSequence
 * @param {ITaskInfo} rst
 * @returns
 */
function addToSequence(taskSequence, rst) {
    if (!rst) {
        return taskSequence;
    }
    if (rst.taskName) {
        let order = 1;
        let len = taskSequence.length + 1;
        if (_.isNumber(rst.order)) {
            order = rst.order;
        }
        else if (_.isFunction(rst.order)) {
            order = rst.order(order);
        }
        if (_.isNumber(order)) {
            if (order > 0 && order < 1) {
                order = Math.round(order * len);
            }
            else if (order === 1) {
                order = len;
            }
            else if (order > len) {
                order = Math.round(order % len);
            }
        }
        if (order < 0) {
            order = 0;
        }
        if (order >= 0 && order < taskSequence.length) {
            taskSequence.splice(order, 0, rst.taskName);
        }
        else {
            taskSequence.push(rst.taskName);
        }
    }
    return taskSequence;
}
exports.addToSequence = addToSequence;
/**
 * filter task sequence. make sure no empty.
 *
 * @param {Src[]} seq
 * @param {(str: string) => boolean} [filter]
 * @returns {Src[]}
 */
function filterTaskSequence(seq, express) {
    let rseq = [];
    express = express || ((it) => !!it);
    _.each(seq, it => {
        if (!it) {
            return;
        }
        if (_.isString(it) && express(it)) {
            rseq.push(it);
        }
        else if (_.isArray(it)) {
            rseq.push(_.filter(it, itm => express(itm)));
        }
    });
    return rseq;
}
/**
 * run task sequence.
 *
 * @protected
 * @param {Gulp} gulp
 * @param {Src[]} tasks
 * @returns {Promise<any>}
 *
 * @memberOf Development
 */
function runSequence(gulp, tasks) {
    tasks = filterTaskSequence(tasks);
    console.log('run tasks : ', chalk.cyan(tasks));
    let run = new Promise((resolve, reject) => {
        let ps = null;
        if (tasks && tasks.length > 0) {
            _.each(tasks, task => {
                if (ps) {
                    ps = ps.then(() => {
                        return startTask(gulp, task);
                    });
                }
                else {
                    ps = startTask(gulp, task);
                }
            });
        }
        else {
            ps = Promise.resolve();
        }
        return ps
            .then(resolve)
            .catch(reject);
    });
    return run.catch(err => {
        console.error(chalk.red(err));
        // process.exit(0);
    });
}
exports.runSequence = runSequence;
/**
 * start task.
 *
 * @param {Gulp} gulp
 * @param {Src} task
 * @returns {Promise<any>}
 */
function startTask(gulp, task) {
    let taskErr = null, taskStop = null;
    return new Promise((reslove, reject) => {
        let tskmap = {};
        _.each(_.isArray(task) ? task : [task], t => {
            tskmap[t] = false;
        });
        taskErr = (err) => {
            process.exit(err);
            console.error(chalk.red(err));
            reject(err);
        };
        taskStop = (e) => {
            tskmap[e.task] = true;
            if (!_.some(_.values(tskmap), it => !it)) {
                reslove();
            }
        };
        gulp.on('task_stop', taskStop)
            .on('task_err', taskErr);
        gulp.start(task);
    })
        .then(() => {
        if (gulp['removeListener']) {
            gulp['removeListener']('task_stop', taskStop);
            gulp['removeListener']('task_err', taskErr);
        }
    }, err => {
        if (gulp['removeListener']) {
            gulp['removeListener']('task_stop', taskStop);
            gulp['removeListener']('task_err', taskErr);
        }
        // process.exit(0);
    });
}
/**
 * run task sequence
 *
 * @export
 * @param {Gulp} gulp
 * @param {(ITask[] | Promise<ITask[]>)} tasks
 * @param {ITaskContext} ctx
 * @returns {Promise<any>}
 */
function runTaskSequence(gulp, tasks, ctx) {
    return Promise.resolve(tasks)
        .then(tasks => {
        let taskseq = toSequence(gulp, tasks, ctx);
        return runSequence(gulp, taskseq);
    });
}
exports.runTaskSequence = runTaskSequence;
